import React, { Component } from 'react';
import d3 from 'd3';

export default class ScatterChart extends Component {
  componentDidMount() {
    d3.select(this.refs.xAxis).call(this.xAxis);
    d3.select(this.refs.yAxis).call(this.yAxis);
  }

  render() {
    const width = 400;
    const height = 400;
    const xAxisWidth = 300;
    const yAxisWidth = 300;
    const padding = {
      top: 30,
      right: 30,
      bottom: 30,
      left: 30
    };

    const center =
      [[0.5, 0.5], [0.7, 0.8], [0.4, 0.9],
       [0.11, 0.32], [0.88, 0.25], [0.75, 0.12],
       [0.5, 0.1], [0.2, 0.3], [0.4, 0.1], [0.6, 0.7]];
    const xScale = d3.scale.linear()
                     .domain([0, 1.2 * d3.max(center, d => d[0])])
                     .range([0, xAxisWidth]);
    const yScale = d3.scale.linear()
                     .domain([0, 1.2 * d3.max(center, d => d[1])])
                     .range([0, yAxisWidth]);
    const data = center.map(circle => ({
      fill: 'black',
      cx: padding.left + xScale(circle[0]),
      cy: height - padding.bottom - yScale(circle[1]),
      r: 5
    }));

    this.xAxis = d3.svg.axis()
               .scale(xScale)
               .orient('bottom');
    yScale.range([yAxisWidth, 0]);
    this.yAxis = d3.svg.axis()
                   .scale(yScale)
                   .orient('left');

    return (
      <svg width={width} height={height}>
      {
        data.map((circle, index) => (
          <circle key={index} {...circle} />
        ))
      }
        <g ref="xAxis"
           className="axis"
           transform={`translate(${padding.left},${height - padding.bottom})`}>
        </g>
        <g ref="yAxis"
           className="axis"
           transform={`translate(${padding.left},${height - padding.top - yAxisWidth})`}>
        </g>
      </svg>
    );
  }
}



